'use client'
import React, { useState } from 'react'
import { Container, Row, Col, Card, Button, Image, Modal, Form } from 'react-bootstrap'

const initialProducts = [
  {
    id: 1,
    name: 'Wireless Headphones',
    price: '$59.99',
    category: 'Electronics',
    image: 'https://via.placeholder.com/200x150',
  },
  {
    id: 2,
    name: 'Running Shoes',
    price: '$89.99',
    category: 'Footwear',
    image: 'https://via.placeholder.com/200x150',
  },
  {
    id: 3,
    name: 'Smartwatch',
    price: '$120.00',
    category: 'Accessories',
    image: 'https://via.placeholder.com/200x150',
  },
  {
    id: 4,
    name: 'Leather Backpack',
    price: '$74.50',
    category: 'Bags',
    image: 'https://via.placeholder.com/200x150',
  },
]

export default function AdminProductCatalogue() {
  const [products, setProducts] = useState(initialProducts)
  const [search, setSearch] = useState('')
  const [showModal, setShowModal] = useState(false)
  const [newProduct, setNewProduct] = useState({ name: '', price: '', category: '', image: '' })

  const handleSearch = (e) => {
    setSearch(e.target.value)
  }

  const filteredProducts = products.filter((product) =>
    product.name.toLowerCase().includes(search.toLowerCase())
  )

  const handleAddProduct = () => {
    setProducts([...products, { ...newProduct, id: products.length + 1 }])
    setNewProduct({ name: '', price: '', category: '', image: '' })
    setShowModal(false)
  }

  return (
    <Container className="py-5">
      <h2 className="mb-4 text-center">Admin Product Catalogue</h2>
      <Row className="mb-4">
        <Col md={6}>
          <Form.Control
            type="text"
            placeholder="Search products..."
            value={search}
            onChange={handleSearch}
          />
        </Col>
        <Col md={6} className="text-end">
          <Button onClick={() => setShowModal(true)}>Add Product</Button>
        </Col>
      </Row>
      <Row>
        {filteredProducts.map((product) => (
          <Col md={6} lg={4} className="mb-4" key={product.id}>
            <Card className="h-100 shadow-sm">
              <Image src={product.image} fluid rounded className="card-img-top" alt={product.name} />
              <Card.Body>
                <Card.Title>{product.name}</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">{product.category}</Card.Subtitle>
                <Card.Text className="fw-bold">{product.price}</Card.Text>
                <div className="d-flex justify-content-between">
                  <Button variant="primary" size="sm">Edit</Button>
                  <Button variant="danger" size="sm">Delete</Button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Add New Product</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>Product Name</Form.Label>
              <Form.Control
                type="text"
                value={newProduct.name}
                onChange={(e) => setNewProduct({ ...newProduct, name: e.target.value })}
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Price</Form.Label>
              <Form.Control
                type="text"
                value={newProduct.price}
                onChange={(e) => setNewProduct({ ...newProduct, price: e.target.value })}
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Category</Form.Label>
              <Form.Control
                type="text"
                value={newProduct.category}
                onChange={(e) => setNewProduct({ ...newProduct, category: e.target.value })}
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Image URL</Form.Label>
              <Form.Control
                type="text"
                value={newProduct.image}
                onChange={(e) => setNewProduct({ ...newProduct, image: e.target.value })}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={handleAddProduct}>
            Add Product
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  )
}
